* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 0 50px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
}

.logo,
.nav {
  display: block;
}

.logo {
  height: 40px;
  width: 40px;
}

.nav {
  align-content: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.nav_list {
  display: flex;
  list-style: none;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.nav_link {
  display: flex;
  align-items: center;
  height: 60px;
  color: black;
  text-decoration: none;
  text-transform: uppercase;
  position: relative;
}

.is-active {
  color: #00acdc;
}

body {
  margin: 0;
}

a.is-active::after,
.nav__link:active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;
  border-radius: 2px;
  background-color: #00acdc;
}

.nav_link:hover {
  color: #00acdc;
}
